$.extend = function(obj, prop) {
  if (!prop) {
    prop = obj;
    obj = $;
  }
  Object.keys(prop).forEach(function(p) {
    if (prop.hasOwnProperty(p)) {
      Object.defineProperty(obj, p, {
        value: prop[p],
        writable: true,
        enumerable: false,
        configurable: true
      });
    }
  });
}

